.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.calculator {
  width: 400px;
  margin: 50px auto;
  background-color: #FFD700;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.display {
  background-color: white;
  padding: 20px;
  margin-bottom: 20px;
  text-align: right;
  font-size: 36px;
  border-radius: 5px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

button {
  padding: 20px;
  font-size: 24px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #FFD700;
  color: #333;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #FFE44D;
}

.operator {
  background-color: #FFB700;
}

.operator:hover {
  background-color: #FFC733;
}

.equals {
  background-color: #FF9500;
}

.equals:hover {
  background-color: #FFB347;
}

.clear {
  background-color: #FFE44D;
}

.clear:hover {
  background-color: #FFF0A5;
}
